'use strict';

import $ from 'jquery';
import toastr from 'toastr';

// Constructor
var RegistrationForm = function () {

  $('#registerForm').validator().on('submit', function (e) {
    if (e.isDefaultPrevented()) {
      toastr.error('Please check the form');
      return;
    } else {
      e.preventDefault();
    }

    var form = {
      'email': $('#registerEmail').val(),
      'password': $('#registerPassword').val(),
      'subscribedToNews': $('#registerSubscribedToNews').is(':checked')
    };

    console.log(form);

    $.ajax({
      type: 'POST',
      url: kit.userEndpoint + '/create',
      data: JSON.stringify(form),
      contentType: "application/json",
      dataType: 'json'
    }).done(function (e) {
      localStorage.setItem('token', e.uuid);
      kit.loggedIn = true;

      window.location.href = '/?msgType=success&msg=Thanks for signing up - you\'re all set to go!.';
    }).fail(function (e) {
      toastr.error('Something went wrong :-(');

      localStorage.removeItem('token');
      kit.loggedIn = false;
      window.update();
    });
    return false;
  });

};

module.exports = RegistrationForm;
